import {
    Button,
    Heading,
    Stack,
    Box,
    Center,
    VStack
  } from '@chakra-ui/react';
  
  export default function Dashboard() {
    return (
      <>
      <Center h={"64px"} bg={"gray.500"}>App Bar</Center>
      <Center p={['5px','5px','5px','100px','100px']}>
        <VStack spacing={"32px"}>
        <Box w={{xl:"1124px"}}><Heading>Dashboard</Heading></Box>
        <Stack spacing={"40px"} direction={['column','column','column','column','row']}>
          <Box w={["356px","356px","356px","728px","356px"]}>
            <VStack spacing={"24px"}>
              <Box h={"100px"} w={["356px","356px","356px","728px","356px"]} bg={"blue"}>L1</Box>
              <Button h={"40px"} w={["356px","356px","356px","728px","356px"]} variant={"solid"}>L2</Button>
              <Box h={"386px"} w={["356px","356px","356px","728px","356px"]} bg={"blue"}>L3</Box>
            </VStack>
          </Box>
          <Box w={["356px","356px","356px","728px","728px"]} h={"800px"} bg={"blue.300"}>
            R1
          </Box>
        </Stack>
        </VStack>
      </Center>
      <Center h={"265px"} bg={"teal.500"}>Footer</Center>
      </>
    );
  }
